/* global window */
import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Checkbox } from '@material-ui/core';
import PropTypes from 'prop-types';
import LoadingCircle from '../../../components/LoadingCircle';
import styles from './styles';


const BlockMember = ({
  classes, blockedAt, loading, onConfirmBlock
}) => (
  <div>
    {
      loading ?
        <div className={classes.checkBoxLoadingView}>
          <LoadingCircle />
          <span>Desativar usurio</span>
        </div>
        :
        <label className={classes.checkBoxViewMembers}>
          <Checkbox
            checked={blockedAt}
            style={styles.checkbox}
            onChange={() => {
              if (window.confirm('Tem certeza em realizar essa ao?')) {
                onConfirmBlock();
              }
            }}
          />
          Desativar usurio
        </label>
    }
  </div>
);

BlockMember.propTypes = {
  loading: PropTypes.bool,
  onConfirmBlock: PropTypes.func,
  classes: PropTypes.object,
  blockedAt: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(BlockMember);
