/* global window */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router';
import { Link } from 'react-router-dom';
import { compose, withState } from 'recompose';
import _get from 'lodash/get';
import _size from 'lodash/size';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Hidden from '@material-ui/core/Hidden';
import FormControl from '@material-ui/core/FormControl';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import AlertBox from '../AlertBox';
import Session from '../../utils/session';
import Tracker from '../../utils/tracker';
import EnterpriseUserService from '../../services/enterprise-user';
import styles from './styles';
import config from '../../config';
import Routes from '../../routes';

const tabMenu = Object.values(Routes).filter((r) => !r.hideMenu);

class Header extends PureComponent {
  state = {
    userName: '',
    userDataError: false,
    sideMenuOpen: false,
    profileMenuOpen: false,
    buildings: [],
    currentBuildingId: ''
  }

  componentDidMount() {
    this.loadData();
  }

  loadData = async () => {
    try {
      const enterpriseUser = Session.getUserData();
      const buildings = _get(enterpriseUser, 'buildings', []);
      const currentBuildingId = _get(enterpriseUser, 'building_id', '');
      const userName = _get(enterpriseUser, 'member.name', '');
      if (_size(buildings) && currentBuildingId) {
        this.setState({ buildings, currentBuildingId, userName });
      } else {
        throw new Error('No buildings');
      }
    } catch {
      this.setState({ userDataError: { error: 'unauthorized' } });
    }
  }

  onLogout = () => {
    this.setState({ profileMenuOpen: false }, () => {
      if (window.confirm('Voc deseja sair?')) {
        const { history } = this.props;
        Tracker.hit('Logout');
        Session.clear();
        history.push('/');
      }
    });
  }

  onToggleSideMenu = () => {
    const { sideMenuOpen } = this.state;
    this.setState({ sideMenuOpen: !sideMenuOpen });
  }

  onChangePage = (e, tabIndex) => {
    const { history } = this.props;
    history.push(tabMenu[tabIndex].path);
  }

  onSetCurrentBuilding = async (e, selected) => {
    const buildingId = selected.key;
    const { currentBuildingId } = this.state;
    if (currentBuildingId !== buildingId) {
      try {
        await EnterpriseUserService.setCurrentBuilding(buildingId);
        const userData = Session.getUserData();
        userData.building_id = buildingId;
        Session.setUserData(userData);
        Tracker.hit('Building - Changed');
        window.location.href = '/';
      } catch {
        this.setState({ userDataError: { error: 'unauthorized' } });
      }
    }
  }

  onToggleProfileMenu = () => {
    const { profileMenuOpen } = this.state;
    this.setState({ profileMenuOpen: !profileMenuOpen });
  }

  renderErrorAlertBox = (error) => {
    const { history } = this.props;
    if (Session.isAuthorized(error)) {
      return (
        <AlertBox
          type="error"
          message="Erro ao carregar dados do servidor"
          actionButton={<b>Recarregar</b>}
          onClickAction={() => window.location.reload()}
        />
      );
    }
    return (
      <AlertBox
        type="error"
        message="Sua sesso expirou, faa login novamente"
        actionButton={<b>Fazer login</b>}
        onClickAction={() => {
          Session.clear();
          history.push('/');
        }}
      />
    );
  }

  renderWebappUpdateAlertBox = () => {
    return (
      <AlertBox
        type="success"
        message="Uma nova atualizao foi instalada."
        actionButton={<b>Continuar</b>}
        onClickAction={() => window.location.reload()}
      />
    );
  }

  renderAlerts = () => {
    const { alertBox, webappUpdated, error } = this.props;
    const { userDataError } = this.state;
    const err = error || userDataError;
    if (webappUpdated) {
      return this.renderWebappUpdateAlertBox();
    }
    if (err) {
      return this.renderErrorAlertBox(err);
    }
    if (alertBox) {
      return alertBox;
    }
    return null;
  }

  renderSideMenu(isSmallScreen) {
    const { classes, history } = this.props;
    const { sideMenuOpen, buildings, currentBuildingId } = this.state;
    return (
      <Drawer
        classes={{ paper: classes.drawer }}
        variant={isSmallScreen ? 'persistent' : 'permanent'}
        anchor="left"
        open={sideMenuOpen}
        ModalProps={{ keepMounted: !isSmallScreen }}
      >
        {
          isSmallScreen &&
            <div className={classes.closeMenuIcon}>
              <IconButton size="medium" onClick={this.onToggleSideMenu}>
                <ChevronLeftIcon />
              </IconButton>
            </div>
        }
        <div className={classes.logoContainer}>
          <img
            src={require('../../assets/images/logo-header.png')}
            alt={config.appName}
            className={classes.logo}
          />
        </div>
        <Divider className={classes.divider} />
        <List>
          <ListItem>
            <FormControl variant="outlined">
              <Select
                className={classes.towerSelect}
                value={currentBuildingId}
                onChange={this.onSetCurrentBuilding}
              >
                {buildings.map((building) => (
                  <MenuItem key={building.id} value={building.id}>
                    {building.name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </ListItem>
        </List>
        <List>
          {tabMenu.map((menu, tabKey) => {
            const subMenus = Object.values(menu.submenus || {});
            const hasSubmenu = Boolean(_size(subMenus));
            let listItemProps = {};
            if (!hasSubmenu) {
              listItemProps = {
                component: Link,
                to: menu.path,
                selected: menu.path === history.location.pathname
              };
            }
            return (
              <div key={tabKey.toString()}>
                <ListItem
                  button
                  key={menu.path}
                  {...listItemProps}
                >
                  {
                    menu.icon &&
                      <ListItemIcon className={classes.listItemIcon}>{menu.icon}</ListItemIcon>
                  }
                  <ListItemText
                    className={hasSubmenu ? classes.listItemTextNoIcon : classes.listItemText}
                    disableTypography
                    primary={menu.label}
                  />
                </ListItem>
                {
                  hasSubmenu &&
                    <List className={classes.submenu}>
                      {
                        subMenus.map((submenu) => {
                          const subMenuPath = `${menu.path}${submenu.path}`;
                          return (
                            <ListItem
                              button
                              key={subMenuPath}
                              component={Link}
                              to={subMenuPath}
                              selected={subMenuPath === history.location.pathname}
                            >
                              <ListItemIcon className={classes.listItemIcon}>
                                {submenu.icon}
                              </ListItemIcon>
                              <ListItemText
                                className={classes.listItemText}
                                disableTypography
                                primary={submenu.label}
                              />
                            </ListItem>
                          );
                        })
                      }
                    </List>
                }
              </div>
            );
          })}
        </List>
      </Drawer>
    );
  }

  render() {
    const { classes } = this.props;
    const { userName, profileMenuOpen } = this.state;
    return (
      <React.Fragment>
        <div className={classes.root}>
          <AppBar position="fixed" className={classes.appBar}>
            <Toolbar>
              <IconButton
                aria-label="open drawer"
                onClick={this.onToggleSideMenu}
                edge="start"
              >
                <MenuIcon />
              </IconButton>
              <div className={classes.profileMenu}>
                <span className={classes.userName} onClick={this.onToggleProfileMenu}>
                  {userName}
                </span>
                <IconButton size="medium" onClick={this.onToggleProfileMenu}>
                  <KeyboardArrowDownIcon />
                </IconButton>
                <Menu
                  keepMounted
                  // PaperProps={{ style: { position: 'fixed', top: 0, right: 20 } }}
                  open={profileMenuOpen}
                  onClose={() => this.setState({ profileMenuOpen: false })}
                  anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
                  transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                >
                  <MenuItem onClick={this.onLogout}>Sair</MenuItem>
                </Menu>
              </div>
            </Toolbar>
          </AppBar>
          <Hidden smDown>
            {this.renderSideMenu(false)}
          </Hidden>
          <Hidden smUp>
            {this.renderSideMenu(true)}
          </Hidden>
          {this.renderAlerts()}
        </div>
      </React.Fragment>
    );
  }
}

Header.propTypes = {
  history: PropTypes.object,
  alertBox: PropTypes.any,
  error: PropTypes.any,
  webappUpdated: PropTypes.bool,
  classes: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withState('webappUpdated', 'setWebappUpdated', false)
)(Header);
