import React, { PureComponent } from 'react';
import _size from 'lodash/size';
import _get from 'lodash/get';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { compose } from 'recompose';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import withRequest from '../../utils/with-request';
import MembersService from '../../services/members';
import Session from '../../utils/session';
import MembersTable from './MembersTable';
import styles from './styles';

const userData = Session.getUserData();

class Members extends PureComponent {
  state = {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }

  clearAlert = () => this.setState({ showAlert: false, alertMsg: '' });

  setAlert = ({ alertMsg, alertType, showAlert }) => this.setState({
    alertMsg, alertType, showAlert
  });

  renderAlertBox = () => {
    const { alertMsg, alertType, showAlert } = this.state;
    if (showAlert) {
      return <AlertBox message={alertMsg} type={alertType} onClose={this.clearAlert} />;
    }
    return null;
  }

  render() {
    const { membersData, membersLoading, membersError, membersFetch, classes } = this.props;
    const members = _get(membersData, 'members');
    const error = membersError;
    const totalMembers = _size(members);
    const loggedMemberId = _get(userData, 'member.id');
    return (
      <div className={classes.root}>
        <Header alertBox={this.renderAlertBox()} error={error} />
        <section>
          <div className={classes.container}>
            <div className={classes.headerView}>
              <aside>
                <Typography variant="h4">
                  <b>{totalMembers ? `Usurios (${totalMembers})` : 'Usurios' }</b>
                </Typography>
              </aside>
            </div>
          </div>
          <div className={classes.container}>
            <MembersTable
              loggedMemberId={loggedMemberId}
              refetch={() => membersFetch(false)}
              setAlert={this.setAlert}
              members={members}
              loading={membersLoading}
            />
          </div>
        </section>
      </div>
    );
  }
}

Members.propTypes = {
  classes: PropTypes.object,
  membersLoading: PropTypes.bool,
  membersError: PropTypes.object,
  membersData: PropTypes.object,
  membersFetch: PropTypes.func
};

const withMembers = withRequest(MembersService.getMembers, { name: 'members' });

export default compose(
  withStyles(styles, { withTheme: true }),
  withMembers
)(Members);
